<template>
<v-navigation-drawer
    :rail="rail"
    permanent
    >
    <v-list nav density="compact">
        <v-list-item
        v-for="item in items"
        :key="item.title"
        :prepend-icon="item.icon"  :value="item.title" :class="rail ? 'my-4 rounded-pill list' : 'rounded-pill list mx-2 pa-4 font-weight-light'"  active-class="activeItem font-weight-bold black--text">
        <p>{{ item.title }}</p>
        </v-list-item>
    </v-list>
    <v-sheet :class="rail ? 'pl-2 rounded-pill list' : 'ma-4 pl-4 rounded-pill list'" >
        <v-row>
            <v-col cols="3" class="">
                <v-avatar color="red" class="mt-1">
                    <span class="text-h5">{{ inputName[0] }}</span>
                </v-avatar>
            </v-col>

            <v-col cols="9">
                <v-row>
                <v-col>
                    <input
                    placeholder="name"
                    v-model="inputName"
                    class="noBorder font-weight-bold"
                    />
                </v-col>
                    
                </v-row>
                <v-row no-gutters justify="start" align="center">
                    <v-col cols="1" >
                    <p class="text-medium-emphasis">@ </p>
                    </v-col>
                    <v-col cols="11">
                        <input
                        placeholder="handle"
                        class="noBorder text-medium-emphasis"
                        />
                    </v-col>
                    
                </v-row>
                
                
            </v-col>

        </v-row>
        <v-row></v-row>
    </v-sheet>
        <!--<v-list-item prepend-icon="mdi-forum" title="About" value="about"></v-list-item>-->
</v-navigation-drawer>
</template>

<script setup>
import { ref, onMounted, watch,  } from 'vue'

const inputName = ref('')
//const handle = ref('')
const rail = ref(false)

const items = [
    { title: "Home", icon: "mdi-home", path: "/home" },
    { title: "Explore", icon: "mdi-pound", path: "/home" },
    { title: "Notifications", icon: "mdi-bell-outline", path: "/home" },
    { title: "Bookmarks", icon: "mdi-bookmark-outline", path: "/home" },
    { title: "Profile", icon: "mdi-account-outline", path: "/home" },
]
//const emit = defineEmits(['updateName'])

watch(inputName, (newVal) => {
    localStorage.setItem('name', newVal)
    //emit('updateName', inputName)
})

const size = () => {
    rail.value = window.matchMedia('(max-width: 1300px)').matches ? true : false 
}


onMounted(() => {
    inputName.value = localStorage.getItem('name') || ''
    size()
    window.addEventListener('resize', size)
    //console.log("Your screen resolution is: " + window.screen.width * window.devicePixelRatio + "x" + window.screen.height * window.devicePixelRatio)
})


</script>

<style >
.activeItem {
  background-color: white;
  color: black !important;
  font-weight: bold;
}
</style>